<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <pie></pie>
    </div>
</template>

<script>
import Vue from 'vue'
import pie from '@/components/pie.js'
Vue.component('pie', pie)
console.log(pie)
export default {
  name: 'dasboard',
  data () {
    return {
      msg: 'IPL Dashboard'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



